.App {
  font-family: sans-serif;
  text-align: left;
  padding: 25px;
}

.flash-card {
  font-size: 24px;
  line-height: 72px;
  width: 500px;
  border: 1px solid black;
  margin: 18px;
  cursor: pointer;
  user-select: none;
  padding: 10px;
}

.flash-card.front {
  background-color: rgb(158, 203, 241);
}

.flash-card.back {
  background-color: rgb(128, 219, 163);
}
